import { Injectable, EventEmitter, OnDestroy } from "@angular/core";
import { Headers, Http, Response } from "@angular/http";
import { Recipe } from "./recipe";
import { Ingredient } from "../ingredient";
import 'rxjs/Rx';
import { Observable } from "rxjs/Observable";
import { Subscription } from "rxjs/Rx";

@Injectable()
export class RecipeService implements OnDestroy {
  private subscription: Subscription;
  recipesChanged = new EventEmitter<Recipe[]>();
  //private recipes: Recipe[] = null;
  
  private recipes: Recipe[] = [
    new Recipe('SUSHI','Schnitzel','Very tasty','http://www.daringgourmet.com/wp-content/uploads/2014/03/Schnitzel-7_edited.jpg',[
      new Ingredient('French Fries',2),
      new Ingredient('Pork meat',1),
    ]),
    new Recipe('SALAD','Caesar Salad','Very tasty','http://assets.simplyrecipes.com/wp-content/uploads/2009/09/caesar-salad-horiz-800.jpg',[])
  ];

  constructor(private http: Http) { 
    //alert('in RecipeService');
}

  getRecipes() {
    return this.recipes;
  }

  getRecipe(id: number) {
    return this.recipes[id];
  }

  deleteRecipe(recipe: Recipe) {
    this.recipes.splice(this.recipes.indexOf(recipe),1);
  }

  addRecipe(recipe: Recipe) {
    this.recipes.push(recipe);
  }

  editRecipe(oldRecipe: Recipe, newRecipe: Recipe) {
    this.recipes[this.recipes.indexOf(oldRecipe)] = newRecipe;
  }

  storeData() {
    const body = JSON.stringify(this.recipes);
    const headers = new Headers({
      'Content-Type': 'application/json'
    });
    return this.http.put('https://recipebook-301ad.firebaseio.com/recipes.json', body, {headers: headers})
    .map((data: Response) => data.json());
  }

  fetchData() {
    //alert('fetchData()');
    this.subscription = this.http.get('https://recipebook-301ad.firebaseio.com/recipes.json')
    .map((response: Response) => response.json())
    .subscribe(
      (data: Recipe[]) => {
        this.recipes = data;
        this.recipesChanged.emit(this.recipes);
      }
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
    //alert('destroying the service');
  }

}



